.Navbar {
    display: flex;
    background-color: rgb(2, 8, 75,);
    position: fixed; 
    top: 0px;
    height: 80px;
    width: 100%;
    margin-top: 0; 
}

.Navbar h1 {
    margin: auto;
    font-size: 20px;
    color: whitesmoke;
}

.navbar-logo {
    width: 60px;
    height: 60px;   
    background-color: gray;
    border-radius: 100%;
    margin: auto;
    margin-left: 10px;
    opacity: .6;
    
}

.navbar-filler {
    flex: 65%;
}

.navbar-home {
    flex: 10%;
}

.navbar-about {
    flex: 10%;
}

.navbar-contact {
    flex: 10%;
}

.navbar-filler-2 {
    flex: 5%;
}

.Navbar a {
    text-decoration: none;
    color: white;
    opacity: .7;
}