.Hero {
    height: 100vh;
    width: 100%;
    background-color: #02084b;
    margin: 0;
}

.hero-container{
    display: flex;
    height: 90vh;
}

/* HERO LEFT START */

.hero-item-left {
    flex: 60%;
    /* background-color: green; */
    height: 90vh;
    width: 100%;
    margin: auto;
}

.hero-item-left-flex {
    display: flex;
    /* background-color: pink; */
    margin-top: 30%; 
}

.hero-item-left-flex h1 {
    font-size: 60px;
    margin: 0;
    margin: 10px;
}

#dev_logo {
    background-color: #02084b;
}

.hero-web {
    color: blue;
}

.hero-development {
    color: lightcoral;
}

.hero-item-left img {
    height: 100px;
    width: 100px;
    background-color: gray;
}

.hero-item-left-flex-filler-1 {
    flex: 10%;
}

.hero-item-left-flex-filler-2 {
    flex: 10%;
}

.why-important {
    color: white;
    font-size: 32px;
    margin-top: 0;
    margin-left: 10%;
}

.learn-more {
    color: white;
    background-color: #6257e3;
    margin-left: 50%;
    margin-right: 25%;
    font-size: 24px;
    padding: 8px;
    padding-bottom: 10px;
    border-radius: 30px;
    opacity: .7;
}


/* HERO LEFT END */


/* HERO RIGHT START */

.hero-item-right {
    flex: 50%;
    /* background-color: blue; */
    height: 90vh;
    width: 100%;
}

.hero-item-right img {
    height: 400px;
    width: 450px;
    background-color: #02084b;
    margin-top: 20%;
}

/* HERO RIGHT END */

/* HERO BOTTOM START */

.hero-container-bottom {
    height: 10vh;
    display: flex;
    /* background-color: burlywood; */
}

.hero-container-bottom img{
    width: 30px;
    height: 30px;  
    background-color: grey;
    margin: 10px;
    margin-bottom: 0;
}

.hero-filler-left  {
    flex: 50%;
}

.hero-filler-right  {
    flex: 50%;
}
/* HERO BOTTOM END */

