#footer {
    background-color: rgb(65, 64, 64);
    color: whitesmoke;
    height: 160px;
    display: flex;
    margin: auto;
}

#footer p {
    margin-top: 80px;
    padding-left: 15%;
    font-size: small;
}

#privacy {
    margin-left: 50px;
}

#privacy:hover {
    cursor: pointer;
    text-decoration: underline;
}


#footer img {
    width: 24px;
    height: 24px;   
    padding: 0px;
    margin-left: 10px;
    margin-top: 76px;
    border-radius: 10px;
}

#footer img:hover {
    cursor: pointer;
}
