.About {
    height: 100vh;
    width: auto ;
    background-color: rgb(205, 226, 43);
    padding: 0;
    display: flex;
}

.About h1 {
    margin: 0;
    font-size: 24px;
}

#importance {
    height: 15vh; 
    margin-top: 15vh;
}

#importance h2 {
    margin: 0;
    background-color: #f7838d;
    width: 430px;
    border-radius: 10px;
    padding: 10px;
    margin: auto;
}

#description {
    height: 15vh;
    margin-top: 15vh;
}
#description h2 {
    margin: 0;
    background-color: #f7838d;
    width: 430px;
    border-radius: 10px;
    padding: 10px;
    margin: auto;
}


.about-left {
    flex: 50%;
    background-color: #02084b;
}   

.about-container {
    display: flex;
    width: 100%;
    height: 70vh;
    background-color: #02084b;
}

.about-container h1 {
    font-size: 20px;
    margin: 0;
    color: white;
}


.about-container-left{
    flex: 50%;
    background-color: #02084b;
}

.about-container-left button {
    padding: 15px;
    margin: 5px;
    margin-left: 10%;
    width: 80%;
    background-color: #6257e3;
    border-radius: 50px;
    margin-top: 25px ;
}

.about-container-right{
    flex: 50%;
    background-color: #02084b;
}

.about-container-right button {
    padding: 15px;
    margin: 5px;
    width: 80%;
    background-color: #6257e3;
    border-radius: 50px;
    margin-top: 35px;
}


.about-right {
    flex: 50%;
    background-color: #02084b;
}

#about-content {
    height: 60vh;
    width: 80%;
    background-color: #6257e3;
    margin: auto;
    border-radius: 15px;
}

#title {
    padding-top: 20px ;
    margin: 10px;
    text-decoration: underline;
}


#desc {
    margin-left: 20px;
    margin-right: 20px;
}

#box_5 {
    margin-top: 60px;
}

.btn {
    border-style: none;
    
}